@import url(https://fonts.googleapis.com/css?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
    font-family: 'Indie Flower', cursive;
}

.wrapper{
    background: -webkit-gradient(linear, left top, right top, from(#58e9e9) ,color-stop(#4df3cf),to(#ec46ad));
    background: -webkit-linear-gradient(left, #58e9e9 ,#4df3cf,#ec46ad);
    background: linear-gradient(to right, #58e9e9 ,#4df3cf,#ec46ad);
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main{
    height: 90vh;
    background: url(/react-final-project/static/media/weather.316751a9.jpg) center center no-repeat;
    box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.65);
    width: 80%;
    margin: 0 auto;
    opacity: 0.8;
    /* transparent(0.2) */
}

.title-container{
    height: 90vh;
    /* background: #fff; */
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    color: purple;
}

.title-container__title{
    font-size: 50px;
    letter-spacing: 2px;
    line-height: 1.3;
    font-family: 'Lobster', cursive;
}

.title-container__subtitle{
    font-style: italic;
    font-weight: 100;
    letter-spacing: 1px;
    line-height: 1.5;
    font-family: 'Dancing Script', cursive;
}

.form-container{
    background-color: lightpink;
    height:90vh;
    padding-top: 100px;
    padding-left: 50px;
}

input[type="text"]{
    background-color: lightpink;
    border: 0;
    border-bottom: solid 1px #f16051;
    width: 30%;
    padding-bottom: 4px;
    color: #fff !important;
    font-weight: lighter;
    letter-spacing: 2px;
    margin-bottom: 30px;
    margin-right: 20px;
    font-size: 20px;
    outline: none;
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #c736eb inset;
    -webkit-text-fill-color: rgb(26, 24, 24) !important;
}

button {
    border: 0;
    padding: 8px 20px;
    margin: 0 2px;
    border-radius: 2px;
    font-weight: lighter;
    letter-spacing: 1px;
    font-size: 15px;
    cursor: pointer;
    background-color: #61f5d0;
    color: purple;
    font-weight: 100;
}

button:active {
    outline: none;
}

.weather {
    width: 60%;
    font-size: 20px;
    font-weight: 200;
    letter-spacing: 2px;
}

.weather--value {
    color: #f16051;
    border-bottom: solid 2px rgba(183, 21, 233, 0.06);
    padding: 20px 0 20px 0;
    font-weight: 400;
}

.weather--value:last-child {
    border: 0;
}

.weather--info {
    color: #fff;
    font-weight: 200;
}

.weather--error {
    color: #f16051;
    font-size: 20px;
    letter-spacing: 1px;
    font-weight: 200;
}
